        },
        {
          src: require("../assets/img/icon-anm-gold.svg"),
          alt: "",
          title_name: "",
          list_name_one: "0",
          list_name_two: "200",
          list_name_three: "0.7"
        },
        {
          src: require("../assets/img/icon-anm-graph.svg"),
          alt: "CFD",
          title_name: "CFD",
          list_name_one: "",
          list_name_two: "",
          list_name_three: ""
        },
        {
          src: require("../assets/img/icon-anm-oil.svg"),
          alt: "CFD",
          title_name: "CFD",
          list_name_one: "A501",
          list_name_two: "200",
          list_name_three: "500"
        },
        {
          src: require("../assets/img/icon-anm-pips.svg"),
          alt: "",
          title_name: "",
          list_name_one: "",
          list_name_two: "",
          list_name_three: ""
        }
      ],
      steps_boxs_items: [
        {
          src: require("../assets/img/step1.svg"),
          content: "MyFXTM"
        },
        {
          src: require("../assets/img/step2.svg"),
          content: ""
        },
        {
          src: require("../assets/img/step3.svg"),
          content: ""
        },
        {
          src: require("../assets/img/step4.svg"),
          content: "MT4MT5"
        },
        {
          src: require("../assets/img/step5.svg"),
          content: ""
        }
      ],
      Surveyiframe: "https://www.surveymonkey.com/r/FXTM_Web",
      myswiper: "",
      i: 0,
      benefits_view: false,
      DropdownBtnStatus: false,
      screenWidth: "",
      steps_view: false,
      riskClosedBtn: false,
      DropdownItemBtnStatus: false,
      DropdownItemBtnStatus2: false,
      DropdownItemBtnStatus4: false,
      DropdownItemBtnStatus5: false,
      DropdownItemBtnStatus3: false,
      DropdownItemBtnStatus6: false,
      showcdrpstatus1: false
    };
  },
  mounted() {
    let _this = this;
    window.addEventListener("scroll", this.handleScroll); // 
    this.$nextTick(function () {
      _this.initData();
      let nav = document.createElement("script");
      nav.type = "application/javascript";
      nav.src = require("../assets/js/iframe_content.js");
      document.body.appendChild(nav);
    });
  },
  watch: {
    screenWidth: {
      handler: function (val, oldVal) {
        if (val < 1014) {
          this.benefits_view = true;
          this.steps_view = true;
          this.showsmallStatus = false;
        } else {
          this.benefits_view = false;
          this.steps_view = false;
          this.showsmallStatus = true;
        }

        immediate: true;
      }
    }
  },
  methods: {
    handleScroll() {
      var scrollTop =
        window.pageYOffset ||
        document.documentElement.scrollTop ||
        document.body.scrollTop;

      var offsetTop = document.querySelector(".header_navbar").offsetHeight;
      var scroll = scrollTop - this.i;
      this.i = scrollTop;
      if (scroll < 0) {
        this.isHeaderFixed = false;
      } else {
        this.isHeaderFixed = true;
      }
      // 
      if (scrollTop > offsetTop) {
        this.isFixed = true;
      } else {
        this.isFixed = false;
        this.isHeaderFixed = false;
      }
    },
    toTop() {
      document.documentElement.scrollTop = 0;
    },

    //
    riskClosed() {
      this.riskClosedBtn = true;
    },
    //
    newpreFerential(val) {
      if (val == 1) {
        this.isShow = !this.isShow;
        this.isShowImg = false;
      } else {
        this.isShowImg = !this.isShowImg;
        this.isShow = false;
      }
    },
    Todrps(val) {
      this.DropdownItemBtnStatus = false;
      this.DropdownItemBtnStatus2 = false;
      this.DropdownItemBtnStatus3 = false;
      this.DropdownItemBtnStatus4 = false;
      this.DropdownItemBtnStatus5 = false;
      this.DropdownItemBtnStatus6 = false;
      this.mainDropdownBtnStatus2 = true;

      if (val == 1) {
        this.DropdownItemBtnStatus = true;
      } else if (val == 2) {
        this.DropdownItemBtnStatus2 = true;
      } else if (val == 3) {
        this.DropdownItemBtnStatus3 = true;
      } else if (val == 4) {
        this.DropdownItemBtnStatus4 = true;
      } else if (val == 5) {
        this.DropdownItemBtnStatus5 = true;
      } else if (val == 6) {
        this.DropdownItemBtnStatus6 = true;
      }

      if (val == "") {
        this.mainDropdownBtnStatus2 = false;
        this.DropdownItemBtnStatus = false;
        this.DropdownItemBtnStatus2 = false;
        this.DropdownItemBtnStatus3 = false;
        this.DropdownItemBtnStatus4 = false;
        this.DropdownItemBtnStatus5 = false;
        this.DropdownItemBtnStatus6 = false;
      }
    },
    //showSurvey
    showSurvey() {
      this.isShowSuvery = !this.isShowSuvery;
    },
    //
    ShowDropdown(val) {
      this.DropdownItemBtnStatus = false;
      this.DropdownItemBtnStatus2 = false;
      this.DropdownItemBtnStatus3 = false;
      this.DropdownItemBtnStatus4 = false;
      this.DropdownItemBtnStatus5 = false;
      this.DropdownItemBtnStatus6 = false;
      if (val == "hide") {
        this.DropdownBtnStatus = false;
        this.mainDropdownBtnStatus = false;
      } else {
        this.DropdownBtnStatus = true;
        this.mainDropdownBtnStatus = true;
        this.mainDropdownBtnStatus2 = false;
      }
    },
    showsmall() {
      this.showsmallStatus = !this.showsmallStatus;
      this.isShowImg = false;
      this.isShow = false;
    },
    cdrp() {
      this.showcdrpstatus1 = true;
    },
    initData() {
      let _this = this;

      window.mParticle &&
        window.mParticle.logPageView("FXTM landing page", {
          origin: window.location.origin,
          page: window.location.pathname,
          domain: window.location.hostname,
          path: window.location.pathname,
          referrer: document.referrer,
          search: window.location.search,
          title: document.title,
          url: window.location.href
        });
      this.screenWidth = document.body.clientWidth;
      window.onresize = () => {
        this.screenWidth = document.body.clientWidth;
      };

      _this.myswiper = new swiper("#swiper1", {
        slidesPerView: 1,

        loop: true,
        autoplay: true,
        loopFillGroupWithBlank: true,
        observer: true, //swiperswiper
        observeParents: true, //swiperswiper
        onSlideChangeEnd: function (swiper) {
          swiper.update();
          _this.myswiper.startAutoplay();
          _this.myswiper.reLoop();
        }
      });

      var myswiper2 = new swiper("#swiper2", {
        slidesPerView: 1,
        autoplay: true,
        loop: true,

        onSlideChangeEnd: function (swiper) {
          swiper.update();
          myswiper2.startAutoplay();
          myswiper2.reLoop();
        },
        pagination: {
          el: ".swiper-pagination",
          clickable: true
        }
      });
      var myswiper3 = new swiper("#swiper3", {
        autoplay: true,
        loop: true,
        pagination: {
          el: "#swiper3 .swiper-pagination",
          clickable: true,

          renderBullet: function (index, className) {
            if (index < _this.steps_boxs_items.length - 1) {
              return (
                '<li class="' +
                className +
                '">' +
                '<span class="cricle">' +
                "<svg width='30' height='2'>" +
                "<line  fill='yellow' y1='1' x2='30' y2='1' stroke='#000' stroke-width='2' stroke-dasharray='3 3'/>" +
                "</svg>" +
                "</span>" +
                "</li>"
              );
            } else {
              return (
                '<li class="' +
                className +
                '">' +
                '<span class="cricle">' +
                "</span>" +
                "</li>"
              );
            }
          }
        }
      });
    }
  }
};
