.navbar-mobile {
  display: none;
  .navbar_mobile_login {
    visibility: hidden;
  }
  .navbar-collapse {
    flex-basis: 100%;
    flex-grow: 1;
    align-items: center;
    .showDropdownItemBtn {
      display: block;
    }
    .showDropdownItemBtn2 {
      display: block;
    }
    .showDropdownItemBtn3 {
      display: block;
    }
    .showDropdownItemBtn4 {
      display: block;
    }
    .showDropdownItemBtn5 {
      display: block;
    }
    .showDropdownItemBtn6 {
      display: block;
    }
    .navbar-mobile_menu {
      background-color: #1a1d30;
      z-index: 100;
      font-size: 1.25rem;
      font-weight: 700;
      text-align: left;
      position: relative;
      text-transform: uppercase;
