        padding-top: 24px !important;
        margin-bottom: 0;
        border-top: solid 2px #fff3;
      }
      .navbar-mobile_nav-item {
        position: relative;
        z-index: 100;
        width: 100%;
        box-sizing: border-box;
        .orange-btn {
          &:hover {
            transition: 0.2s;
            background-color: transparent !important;
            color: #ff4521 !important;
          }
        }
        .mega-menu_promo-block {
          max-width: 100%;
          flex-direction: row;
          display: flex;
          color: #fff;
        }
        .navbar-mobile_item-link-text {
          height: 52px;
          padding: 12px 20px;
          border-left: solid 8px transparent;
        }
        .navbar-mobile_dropdown-link {
          display: flex;
          justify-content: space-between;
          padding: 12px 20px;
        }
        .submenu-container {
          margin-bottom: 10px;
          padding: 0 15px 10px 15px;
          overflow: hidden;
        }

        .navbar-mobile_dropdown-link-text {
          height: 52px;
          text-transform: none;
          padding-left: 15px;
          font-weight: 400;
        }
        a {
          text-decoration: none;
          color: #fff;
          position: relative;
          box-sizing: border-box;
        }
        svg {
          pointer-events: none;
        }
        .mega-menu_promo-block {
          max-width: 100%;
          flex-direction: row;
          display: flex;
          color: #fff;
          div {
            padding: 0 8px;
            margin-bottom: 8px;
            display: inherit;
          }
          .mega-menu_promo-block_content {
            min-width: 150px;
            flex-direction: column;
            .mega-menu_promo-block_title {
              font-size: 20px;
              line-height: 28px;
            }
            .mega-menu_promo-block_subtitle {
              font-size: 12px;
              line-height: 18px;
              p {
                margin-top: 0;
                margin-bottom: 1rem;
              }
            }
            .mega-menu_promo-block_button {
              margin-top: 0;
              .green-btn_arrow {
                padding: 0;
                display: inline-block;
                color: #a4e22d !important;
                font-weight: 700;
                border-radius: 32px;

                text-align: center;
                transition: 0.2s;
                .arrow-right {
                  padding: 3px;
                  display: inline-block;
                  border: solid #a4e22d;
                  border-width: 0 3px 3px 0;
                  transform: rotate(-45deg);
                  -webkit-transform: rotate(-45deg);
                }
              }
            }
          }
        }
      }

      .nav-item-contact {
        border-top: solid 2px rgba(255, 255, 255, 0.2);
        height: 94px;
        box-sizing: border-box;
        .open-acc-btn {
          font-size: 0.8125rem;
          font-weight: 400;
          padding: 9px 32px;
          background-color: #ff4521 !important;
          color: #fff !important;
          transition: 0.2s;
          border: 1px solid #ff4521 !important;
          border-radius: 32px;

          text-align: center;
        }
      }
      .level-1 {
        border-left: solid 8px transparent;
        &:hover {
          border-left: solid 8px #ff4521;
        }
      }
      .level-2 {
        width: 100%;
        margin-bottom: 5px;
        margin-top: 5px;
        padding: 0 15px;
        a {
          &:hover {
            background-color: rgba(255, 255, 255, 0.14);
            border-radius: 8px;
          }
        }
      }

      .hide1 {
        display: none;
      }
      .level-3 .navbar-mobile_arrow {
        display: none;
      }
      .showcdrp {
        display: block !important;
      }
      .nav-item-back-btn {
        background-color: rgba(255, 255, 255, 0.08);
        text-align: left;
        height: 70px;

        .navbar-mobile_item-link-back-btn {
          display: block;
          padding: 22px 32px;
          .navbar-mobile_arrow-icon {
            margin-bottom: 2.5px;
            transform: rotate(180deg);
          }
        }
      }
      .nav-item-title {
        box-sizing: border-box;
        border-left: solid 8px #ff4521;
        padding: 12px 25px 12px 22px;
        color: #fff;
        font-size: 1.25rem;
        font-weight: 700;
        height: 52px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-right: 10px;
      }
      a {
        text-decoration: none;
        color: #fff;
        position: relative;
      }
      svg {
        pointer-events: none;
      }
    }
    .navbar-nav {
      padding-left: 0;
      margin-bottom: 0;
      list-style: none;
    }
  }
  .navbarNavDropdown_hide {
    display: block !important;
  }
  .navbarNavDropdown_hide1 {
    display: none;
  }
}

